import { ValueCalculationComponent } from './value-calculation/value-calculation.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  { path: 'calculateyourvalue', component: ValueCalculationComponent },
  { path: '',
  redirectTo: 'calculateyourvalue',
  pathMatch: 'full'
},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
